import { useEffect } from "react";
import { useDispatch, useSelector } from "react-redux";
import { getPricingTypes } from "../lookups/LookupsActions";

const usePricingTypes = () => {
  const { pricingTypes } = useSelector((state) => state.lookups);
  const dispatch = useDispatch();

  useEffect(() => {
    if (!pricingTypes?.isFetched) {
      dispatch(getPricingTypes());
    }
  }, []);

  return pricingTypes?.progressing ? false : pricingTypes?.data;
};

export default usePricingTypes;
