import { Form } from "antd";
import React, { useMemo } from "react";
import { useDispatch, useSelector } from "react-redux";
import { setRateShopLaneStopTab } from "../../../../redux/RateShop/RateShopActions";
import Tab from "../../../components/Tabs/Tab";
import { getLaneTabName } from "./LaneTabsDetails";
import StopDetails from "./StopDetails";
import { useLocation } from "react-router-dom";
import { useTranslation } from 'react-i18next';
import { routeFileSelector } from '../../../../utilz/i18n';

const StopsTab = ({ form, lanes }) => {
  const dispatch = useDispatch();
  const { activeLaneStopTab, itemsSumValues } = useSelector(
    (state) => state.rateShop
  );
  const lanesFormData = Form.useWatch("lanes", form);
  const { pathname: activeTab } = useLocation();
  const namespace = useMemo(() => routeFileSelector(activeTab), [activeTab]);
  const { t } = useTranslation([namespace, 'translation']);

  return (
    <Tab
      contents={lanes.map(({ name }, index) => ({
        title: getLaneTabName(
          lanesFormData ? lanesFormData[Number(name)] : {},
          index
        ),
        content: (
          <StopDetails
            form={form}
            laneDetails={lanesFormData?.[name] ? lanesFormData?.[name] : {}}
            laneName={name}
            itemsSumValues={itemsSumValues}
          />
        ),
        contentKey: String(name),
      }))}
      onChange={(key) => dispatch(setRateShopLaneStopTab(key))}
      defaultActiveKey={"1"}
      activeKey={activeLaneStopTab}
    />
  );
};

export default StopsTab;
