import { Form } from "antd";
import React, { useEffect, useMemo } from "react";
import { useDispatch, useSelector } from "react-redux";
import { RATE_SHOP_TYPES } from "../../../../contants/common";
import { setRateShopGlobalMeasurementUnit } from "../../../../redux/RateShop/RateShopActions";
import FormDatePicker from "../../../components/Inputs/FormDatePicker";
import FormSelectBox from "../../../components/Inputs/FormSelectBox";
import CargoTypeSelector from "../../../components/Selectors/CargoTypeSelector";
import CurrencySelector from "../../../components/Selectors/CurrencySelector";
import CustomerNameSelector from "../../../components/Selectors/CustomerNameSelector";
import MeasurementUnitsSelector from "../../../components/Selectors/MeasurementUnitsSelector";
import PricingBasisSelector from "../../../components/Selectors/PricingBasisSelector";
import PricingTypeSelector from "../../../components/Selectors/PricingTypeSelector";
import ShipmentBranchSelector from "../../../components/Selectors/ShipmentBranchSelector";
import ShipmentTermsSelector from "../../../components/Selectors/ShipmentTermsSelector";
import ShipmentTypeSelector from "../../../components/Selectors/ShipmentTypeSelector";
import { useLocation } from "react-router-dom";
import { useTranslation } from 'react-i18next';
import { routeFileSelector } from '../../../../utilz/i18n';

const GeneralRateShopFields = ({ form }) => {
  const dispatch = useDispatch();
  const { selectedCompany } = useSelector((state) => state.auth);
  const shipmentDate = Form.useWatch("shipmentDate", form);
  const deliveryDate = Form.useWatch("deliveryDate", form);
  const measurementUit = Form.useWatch("measurementUit", form);
  const { pathname: activeTab } = useLocation();
  const namespace = useMemo(() => routeFileSelector(activeTab), [activeTab]);
  const { t } = useTranslation([namespace, 'translation']);


  useEffect(() => {
    if (
      deliveryDate < shipmentDate ||
      (Boolean(shipmentDate) === true && !deliveryDate)
    ) {
      form.setFieldValue("deliveryDate", shipmentDate);
    }
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [shipmentDate]);

  useEffect(() => {
    dispatch(setRateShopGlobalMeasurementUnit(measurementUit));
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [measurementUit]);

  return (
    <div className="mt-10">
      <CustomerNameSelector
        label={t('translation:default.fields.customer.label')}
        rules={[{ required: true, message:  t('translation:default.fields.customer.message') }]}
        name={"customerKey"}
      />
      <ShipmentBranchSelector
        customerId={selectedCompany?.companyId}
        label={t('translation:default.fields.branch.label')}
        name={"branchKey"}
        showAll={true}
      />
      <CurrencySelector
        name={"currencyCode"}
        rules={[{ required: true, message: t('translation:rateShop.rateShop.currencyCode.message') }]}
      />
      <PricingTypeSelector
        name={"pricingType"}
        rules={[{ required: true, message: t('translation:rateShop.rateShop.pricingType.message') }]}
      />
      <PricingBasisSelector
        name={"pricingBasis"}
        rules={[{ required: true, message: t('translation:rateShop.rateShop.pricingBasis.message')  }]}
      />
      <FormSelectBox
        options={RATE_SHOP_TYPES}
        label={t('translation:rateShop.rateShop.rateSelection.label')}
        placeholder={t('translation:rateShop.rateShop.rateSelection.label')}
        name={"timeFrame"}
        rules={[{ required: true, message: t('translation:rateShop.rateShop.rateSelection.message') }]}
      />
      <ShipmentTermsSelector
        label={t('translation:default.fields.paymentType.label')}
        name={"shipmentTerms"}
        rules={[{ required: true, message: t('translation:default.fields.shipmentTerms.message') }]}
      />
      <MeasurementUnitsSelector
        label={t('translation:rateShop.rateShop.measurementUit.label')}
        editMode={true}
        name={"measurementUit"}
        rules={[
          { required: true, message: t('translation:rateShop.rateShop.measurementUit.message') },
        ]}
      />
      <CargoTypeSelector
        mode={"multiple"}
        margin={"mb-15"}
        name={"cargoType"}
        rules={[{ required: true, message: t('translation:default.fields.cargoType.message') }]}
      />

      <ShipmentTypeSelector
        name={"shipmentType"}
        rules={[{ required: true, message: t('translation:default.fields.shipmentType.message') }]}
      />

      <FormDatePicker
        label={t('translation:default.fields.shipDate.label')}
        name={"shipmentDate"}
        rules={[{ required: true, message: t('translation:rateShop.rateShop.shipmentDate.message') }]}
      />
      <FormDatePicker
        label={t('translation:default.fields.deliveryDate.label')}
        name={"deliveryDate"}
        disableFuture={false}
        disabledTargetDate={shipmentDate}
      />
    </div>
  );
};

export default GeneralRateShopFields;
