import { Col, Divider, Row } from "antd";
import SvgConverter from "../../../components/Common/SvgConverter";
import { ReactComponent as LocationIcon } from "../../../../assets/icons/location-pin-green.svg";
import { SHIPMENT_STOP_TYPES } from "../../../../contants/ShipmentEnums";
import SecondaryLabel from "../../../components/Labels/SecondaryLabel";
import CountrySelector from "../../../components/Selectors/CountrySelector";
import PostalCodeInput from "../../../components/Selectors/PostalCodeInput";
import StateSelector from "../../../components/Selectors/StateSelector";
import FormInput from "../../../components/Inputs/FormInput";
import PortNameSelector from "../../../components/Selectors/PortNameSelector";
import { useMemo } from "react";
import PortTypeSelector from "../../../components/Selectors/PortTypeSelector";
import HiddenFormInput from "../../../components/Inputs/HiddenFormInput";
import { useLocation } from "react-router-dom";
import { useTranslation } from 'react-i18next';
import { routeFileSelector } from '../../../../utilz/i18n';


const StopCard = ({
  stopType = "",
  showHeader = false,
  formMode,
  name,
  laneDetails,
  handleZipDetails = () => {},
}) => {
  const { pathname: activeTab } = useLocation();
  const namespace = useMemo(() => routeFileSelector(activeTab), [activeTab]);
  const { t } = useTranslation([namespace, 'translation']);
  
  const name_prefix = useMemo(
    () =>
      formMode === "stop"
        ? ""
        : stopType === SHIPMENT_STOP_TYPES.ORIGIN
        ? "ORIGIN_"
        : "DEST_",
    [stopType]
  );

  const editMode = useMemo(
    () => (formMode === "stop" ? Boolean(laneDetails?.editMode) : false),
    [formMode, laneDetails?.editMode]
  );

  return (
    <Row className="flex-nowrap flex-direction-column">
      {showHeader === true ? (
        <Col className="mb-12">
          <Row align={"middle"} gutter={15}>
            <Col>
              <div>
                <Row gutter={10}>
                  <Col className="lh-0">
                    <SvgConverter
                      Icon={LocationIcon}
                      color={
                        stopType === SHIPMENT_STOP_TYPES.ORIGIN
                          ? "success"
                          : "danger-3"
                      }
                    />
                  </Col>
                  <Col>
                    <SecondaryLabel
                      className={`f-14 fw-500 text-${
                        stopType === SHIPMENT_STOP_TYPES.ORIGIN
                          ? "success"
                          : "danger-3"
                      }`}
                    >
                      {stopType === SHIPMENT_STOP_TYPES.ORIGIN
                        ? t('translation:default.fields.origin')
                        : t('translation:default.fields.destination')}
                    </SecondaryLabel>
                  </Col>
                </Row>
              </div>
            </Col>
            <Col className="flex-1">
              <Divider className="m-0 border-top-border" />
            </Col>
          </Row>
        </Col>
      ) : (
        <></>
      )}

      <Col>
        <div>
          <Row gutter={15}>
            <Col sm={8}>
              <CountrySelector
                editMode={editMode}
                name={[name, `${name_prefix}countryCode`]}
                rules={[{ required: true, message:  t('translation:default.fields.country.message') }]}
              />
              <PostalCodeInput
                editMode={editMode}
                name={[name, `${name_prefix}postalCode`]}
                countryCode={laneDetails?.[`${name_prefix}countryCode`]}
                handleZipDetails={handleZipDetails}
                rules={[
                  { required: true, message: t('translation:default.fields.postalCode.message') },
                ]}
              />
              <StateSelector
                editMode={editMode}
                name={[name, `${name_prefix}stateCode`]}
                countryCode={laneDetails?.[`${name_prefix}countryCode`]}
                rules={[{ required: true, message: t('translation:default.fields.state.message') }]}
              />
              <FormInput
                label={t('translation:default.fields.city.label')}
                margin={"m-0"}
                editMode={editMode}
                name={[name, `${name_prefix}city`]}
              />
            </Col>
            <Col sm={8}>
              <PortTypeSelector
                margin={"mb-12"}
                name={[name, `${name_prefix}portType`]}
                editMode={editMode}
              />
              <PortNameSelector
                label={t('translation:rateShop.rateShop.port')}
                editMode={editMode}
                margin={"mb-15"}
                name={[name, `${name_prefix}portCode`]}
                portType={laneDetails?.[`${name_prefix}portType`]}
                countryCode={laneDetails?.[`${name_prefix}countryCode`]}
                stateCode={laneDetails?.[`${name_prefix}stateCode`]}
              />
              <FormInput
                label={t('translation:rateShop.rateShop.pieces.label')}
                editMode={formMode === "stop" ? editMode : true}
                inputType={"number"}
                margin={"mb-15"}
                name={[name, `${name_prefix}numberOfPieces`]}
                rules={[{ required: true, message: t('translation:rateShop.rateShop.pieces.message') }]}
              />
              <FormInput
                label={t('translation:default.fields.weight')}
                editMode={formMode === "stop" ? editMode : true}
                inputType={"number"}
                name={[name, `${name_prefix}weight`]}
                rules={[{ required: true, message: t('translation:rateShop.rateShop.weight.message')}]}
              />
            </Col>
            <Col sm={8}>
              <FormInput
                margin="mb-40"
                label={t('translation:rateShop.rateShop.legDistance')}
                editMode={false}
                inputType={"number"}
                name={[name, `${name_prefix}legMile`]}
              />
              <FormInput
                label={t('translation:rateShop.rateShop.tripDistance')}
                editMode={false}
                inputType={"number"}
                name={[name, `${name_prefix}tripMile`]}
              />
            </Col>
          </Row>
        </div>
      </Col>
      <HiddenFormInput name={[name, "editMode"]} />
      <HiddenFormInput name={[name, "isNew"]} />
    </Row>
  );
};

export default StopCard;
