import { Col, Row } from "antd";
import { sumBy } from "lodash";
import React, { useMemo } from "react";
import AccessorialDetailsIndividual from "./AccessorialDetailsIndividual";
import FooterFieldCard from "./FooterFieldCard";
import RateShopIndividualDetailsLayout from "./RateShopIndividualDetailsLayout";
import { useLocation } from "react-router-dom";
import { useTranslation } from 'react-i18next';
import { routeFileSelector } from '../../../../../utilz/i18n';


const AccessorialDetails = ({ accessorials }) => {
  const { pathname: activeTab } = useLocation();
  const namespace = useMemo(() => routeFileSelector(activeTab), [activeTab]);
  const { t } = useTranslation([namespace, 'translation']);

  return (
    <RateShopIndividualDetailsLayout>
      <RateShopIndividualDetailsLayout.ScrollContainer>
        {accessorials.map((data, key) => (
          <AccessorialDetailsIndividual key={key} data={data} />
        ))}
      </RateShopIndividualDetailsLayout.ScrollContainer>
      <RateShopIndividualDetailsLayout.FooterContainer>
        <div className="px-15 pb-15 pt-10">
          <Row justify={"end"} align={"middle"}>
            <Col className="w-19">
              <FooterFieldCard
                title={t('translation:rateShop.totalAccessorialCharge')}
                value={sumBy(
                  accessorials,
                  ({ auditedChargeAmount }) => auditedChargeAmount
                )}
              />
            </Col>
          </Row>
        </div>
      </RateShopIndividualDetailsLayout.FooterContainer>
    </RateShopIndividualDetailsLayout>
  );
};

export default AccessorialDetails;
