import { Col, Row } from "antd";
import React, { useMemo, useState } from "react";
import IconButton from "../../../components/Buttons/IconButton";
import FilterCard from "../../../components/Cards/FilterCard";
import SvgConverter from "../../../components/Common/SvgConverter";
import PrimaryLabel from "../../../components/Labels/PrimaryLabel";
import AccessorialsFilterPopOver from "../../Shipment/components/common/Accessorials/AccessorialsFilterPopOver";
import { ReactComponent as FilterIcon } from "../../../../assets/icons/filter.svg";
import { useDispatch, useSelector } from "react-redux";
import { getTaxListInSuccess } from "../../../../redux/RateShop/RateShopActions";
import ContentSpinner from "../../../components/Spinners/ContentSpinner";
import FormCheckBox from "../../../components/Inputs/FormCheckBox";
import { filterRateShopAdditionalFieldsVersionTwo } from "../../../../utilz/utilz";
import { cloneDeep, filter, findIndex, orderBy, uniqBy } from "lodash";
import { useLocation } from "react-router-dom";
import { useTranslation } from 'react-i18next';
import { routeFileSelector } from '../../../../utilz/i18n';


const TaxList = () => {
  const dispatch = useDispatch();
  const { taxList } = useSelector((state) => state.rateShop);
  const [filterOpen, setFilterOpen] = useState(false);
  const { pathname: activeTab } = useLocation();
  const namespace = useMemo(() => routeFileSelector(activeTab), [activeTab]);
  const { t } = useTranslation([namespace, 'translation']);

  const handleFilter = (filterData) => {
    const filteredList = filterRateShopAdditionalFieldsVersionTwo(
      taxList?.data,
      filterData,
      "codeDesc"
    );
    dispatch(
      getTaxListInSuccess({
        data: uniqBy(
          [
            ...filteredList.map((row) => ({ ...row, hide: false })),
            ...orderBy(
              taxList?.data.map((row) => ({ ...row, hide: true })),
              ["codeDesc"],
              ["asc"]
            ),
          ],
          ({ codeId }) => codeId
        ),
      })
    );
    setFilterOpen(false);
  };

  const handleCheckBox = (e, codeId) => {
    let data = cloneDeep(taxList?.data);
    const index = findIndex(data, { codeId });
    data[index].isChecked = e?.target?.checked;
    dispatch(
      getTaxListInSuccess({
        data,
      })
    );
  };

  const handleSelectAll = (e) => {
    const value = e?.target?.checked;
    let data = cloneDeep(taxList?.data);
    data = data.map((row) => ({ ...row, isChecked: value }));
    dispatch(
      getTaxListInSuccess({
        data,
      })
    );
  };

  const isSelectedAll = useMemo(
    () =>
      filter(taxList?.data, { isChecked: true })?.length ===
      taxList?.data?.length,
    [taxList?.data]
  );

  return (
    <FilterCard
      className={`h-100 overflow-hidden d-flex flex-direction-column background-white`}
      shadow={true}
      showBorder={false}
      headerClassName={"background-selected"}
      customHeader={
        <div className="w-100">
          <Row justify={"space-between"} align={"middle"}>
            <Col>
              <FormCheckBox
                checked={isSelectedAll}
                onChange={handleSelectAll}
                className={"m-0"}
                checkBoxClass={"normal-checkbox"}
                label={
                  <PrimaryLabel className={"text-black"}>
                    {t('translation:rateShop.taxDetails')}
                  </PrimaryLabel>
                }
              />
            </Col>
            <Col>
              <AccessorialsFilterPopOver
                open={filterOpen}
                setOpen={setFilterOpen}
                handleFilter={handleFilter}
                accessorials={taxList?.data}
                accessors={{ label: "codeDesc", value: "codeDesc" }}
                placement={"leftBottom"}
              >
                <IconButton onClick={() => setFilterOpen(true)}>
                  <SvgConverter Icon={FilterIcon} color={"black"} />
                </IconButton>
              </AccessorialsFilterPopOver>
            </Col>
          </Row>
        </div>
      }
    >
      <div className="flex-1 overflow-hidden d-flex flex-direction-column">
        {taxList?.progressing ? (
          <ContentSpinner />
        ) : (
          <div className="h-100 custom-scroll-bar">
            {taxList?.data?.map(({ codeId, codeDesc, hide, isChecked }) => (
              <div
                className={`px-15 py-10 border-bottom-border ${
                  hide ? "d-none" : ""
                }`}
                key={codeId}
              >
                <FormCheckBox
                  checkBoxClass="normal-checkbox"
                  label={
                    <span className="f-14 text-dark fw-600">
                      {codeDesc} ({codeId})
                    </span>
                  }
                  className={"m-0"}
                  checked={isChecked}
                  onChange={(e) => handleCheckBox(e, codeId)}
                />
              </div>
            ))}
          </div>
        )}
      </div>
    </FilterCard>
  );
};

export default TaxList;
