import React from "react";
import usePricingTypes from "../../../redux/hooks/usePricingTypes";
import FormSelectBox from "../Inputs/FormSelectBox";
import i18n from 'i18next';

const PricingTypeSelector = ({
  placeholder = i18n.t('translation:default.fields.pricingType.placeholder'),
  label = i18n.t('translation:default.fields.pricingType.label'),
  rules = [],
  name,
  editMode,
  value,
  tooltip,
  margin = "mb-15",
  mode = "",
}) => {
  const pricingTypes = usePricingTypes();

  return (
    <FormSelectBox
      loading={pricingTypes === undefined}
      options={
        pricingTypes
          ? pricingTypes.map((row) => ({
              label: row.codeDesc,
              value: row.codeId,
            }))
          : []
      }
      placeholder={placeholder}
      label={label}
      margin={margin}
      rules={rules}
      name={name}
      editMode={editMode}
      value={value}
      tooltip={tooltip}
      mode={mode}
    />
  );
};

export default PricingTypeSelector;
