import React, { useEffect, useState } from "react";
import { useSelector } from "react-redux";
import { getCustomerCompanyListEffect } from "../../../redux/auction/AuctionEffects";
import { getCustomerName } from "../../../redux/POV/POVEffects";
import { getCompanyListEffect } from "../../../redux/auth/AuthEffects";
import FormSelectBox from "../Inputs/FormSelectBox";
import i18n from 'i18next';

const CustomerNameSelector = ({
  placeholder = i18n.t('translation:default.fields.customerName.placeholder'),
  label = i18n.t('translation:default.fields.customerName.label'),
  rules = [],
  name,
  editMode,
  margin,
  readOnly,
  disabled,
  mode = "",
  form,
  id = "",
  setDetafuleValue = false,
  callbackSetValue = () => {}
}) => {
  const [options, setOptions] = useState([]);
  const [loading, setLoading] = useState(false);
  const [defaultParams, setDefaultParams] = useState({});
  const { selectedCompany } = useSelector((state) => state.auth);
  const {
    user: { data: userDetails },
  } = useSelector((state) => state?.auth);

  useEffect(() => {
    if (selectedCompany && selectedCompany.companyId) {
      getCustomerNameList(selectedCompany.companyId);
    }
    if (userDetails?.userType === "CUST") {
      setDefaultParams({
        value: selectedCompany?.companyId,
        label: selectedCompany?.companyName,
      });
      if (form) {
        form.setFieldValue("customerKey", selectedCompany?.companyId);
      }
    }
  }, [selectedCompany, form]);

  const getCustomerNameList = async (companyId) => {
    try {
      setLoading(true);
      let param = {};
      let result = [];
      let options = {};
      if (userDetails?.userType === "CARR") {
        param = { carrierKey: companyId };
      } else {
        param = { VendorKey: companyId };
      }

      if (userDetails?.userType === "CARR") {
        const { data } = await getCustomerCompanyListEffect(param);
        result = data;
        options = result.map((row) => ({
          label: row.customerName,
          value: row.customerKey,
        }));
      } else if (userDetails?.userType === "CUST") {
        const { data } = await getCompanyListEffect();
        result = data;
        options = result.map((row) => ({
          label: row.companyName,
          value: row.companyId,
        }));
      } else {
        // userDetails?.userType === "VEND"
        const { data } = await getCustomerName(param);
        result = data;
        options = result.map((row) => ({
          label: row.customerName,
          value: row.customerKey,
        }));
      }
      setOptions(options);
      if(setDetafuleValue === true){
        setTimeout(() => {
          form.setFieldValue("customerKey", options?.[0]?.value);
          callbackSetValue(options?.[0]?.value);
        }, 200);
      }
      setLoading(false);
    } catch (error) {
      console.log(error);
      setOptions([]);
      setLoading(false);
    }
  };

  return (
    <FormSelectBox
      loading={loading}
      options={options}
      placeholder={placeholder}
      label={label}
      rules={rules}
      name={name}
      editMode={editMode}
      margin={margin}
      readOnly={readOnly}
      disabled={disabled}
      mode={mode}
      defaultValue={defaultParams?.label}
      id={id}
    />
  );
};

export default CustomerNameSelector;
