import { useEffect } from "react";
import { useDispatch, useSelector } from "react-redux";
import { getPricingBasis } from "../lookups/LookupsActions";

const usePricingBasis = () => {
  const { pricingBasis } = useSelector((state) => state.lookups);
  const dispatch = useDispatch();

  useEffect(() => {
    if (!pricingBasis?.isFetched) {
      dispatch(getPricingBasis());
    }
  }, []);

  return pricingBasis?.progressing ? false : pricingBasis?.data;
};

export default usePricingBasis;
