import { Col, Form, Row, Tabs } from "antd";
import React, { useMemo } from "react";
import { useDispatch, useSelector } from "react-redux";
import {
  setRateShopLaneTab,
  setRateShopStopsDrawer,
} from "../../../../redux/RateShop/RateShopActions";
import Button from "../../../components/Buttons/Button";
import FilterCard from "../../../components/Cards/FilterCard";
import PrimaryLabel from "../../../components/Labels/PrimaryLabel";
import Drawer from "../../../components/Modals/Drawer";
import LaneDetails from "./LaneDetails";
import StopsTab from "./StopsTab";
import { useLocation } from "react-router-dom";
import { useTranslation } from 'react-i18next';
import { routeFileSelector } from '../../../../utilz/i18n';


const LaneTabsDetails = () => {
  const [form] = Form.useForm();
  const { pathname: activeTab } = useLocation();
  const namespace = useMemo(() => routeFileSelector(activeTab), [activeTab]);
  const { t } = useTranslation([namespace, 'translation']);

  const { activeLaneTab, stopsDrawerVisible } = useSelector(
    (state) => state.rateShop
  );
  const dispatch = useDispatch();
  const handleTabClick = (key) => {
    dispatch(setRateShopLaneTab(key));
  };

  const lanesFormData = Form.useWatch("lanes", form);

  // console.log("activeLaneTab", activeLaneTab);

  const handleDeleteLane = (removeFun = () => {}, currentKey) => {
    const nextKey = Number(currentKey) - 1;
    dispatch(setRateShopLaneTab(String(nextKey < 0 ? 0 : nextKey)));
    removeFun(Number(currentKey));
  };

  const handleNewLane = (addFun = () => {}) => {
    const lanes = form.getFieldValue("lanes");
    dispatch(setRateShopLaneTab(String(lanes.length)));
    addFun();
  };

  return (
    <Form form={form} name={"laneForm"} initialValues={{ lanes: [{}] }}>
      <Form.List name={"lanes"}>
        {(lanes, { add, remove }) => (
          <>
            <FilterCard
              className="background-white"
              customHeader={
                <div className="w-100">
                  <Row justify={"space-between"} align={"middle"}>
                    <Col>
                      <PrimaryLabel className="f-14 text-black">
                        {t('translation:rateShop.rateShop.laneDetails')}
                      </PrimaryLabel>
                    </Col>
                    <Col>
                      <Row gutter={15}>
                        <Col>
                          <Button onClick={() => handleNewLane(add)}>
                            {t('translation:rateShop.rateShop.addLane')}
                          </Button>
                        </Col>
                        <Col>
                          <Button
                            variant="danger"
                            type="default"
                            onClick={() =>
                              handleDeleteLane(remove, activeLaneTab)
                            }
                            disabled={lanesFormData?.length === 1}
                          >
                               {t('translation:rateShop.rateShop.deleteLane')}
                          </Button>
                        </Col>
                      </Row>
                    </Col>
                  </Row>
                </div>
              }
            >
              <div className=" p-15">
                <Tabs
                  className="page-level-tab-container tabs-margin-none"
                  onChange={handleTabClick}
                  activeKey={activeLaneTab}
                >
                  {lanes?.map(({ name }, index) => (
                    <Tabs.TabPane
                      key={name}
                      tab={getLaneTabName(
                        lanesFormData ? lanesFormData[Number(name)] : {},
                        index
                      )}
                    >
                      <LaneDetails
                        name={name}
                        data={lanesFormData ? lanesFormData[Number(name)] : {}}
                        form={form}
                      />
                    </Tabs.TabPane>
                  ))}
                </Tabs>
              </div>
            </FilterCard>
            <Drawer
              visible={stopsDrawerVisible}
              onClose={() => dispatch(setRateShopStopsDrawer(false))}
              headerContents={
                <PrimaryLabel
                  className={"text-white"}
                > {t('translation:rateShop.rateShop.actualCarrierRateShopping')}</PrimaryLabel>
              }
              width={"70%"}
            >
              <StopsTab form={form} lanes={lanes} />
            </Drawer>
          </>
        )}
      </Form.List>
    </Form>
  );
};

export default LaneTabsDetails;

export const getLaneTabName = (laneDetails, index) => {
  if (
    laneDetails?.ORIGIN_postalCode &&
    laneDetails?.DEST_postalCode &&
    laneDetails?.ORIGIN_city &&
    laneDetails?.DEST_city
  ) {
    return (
      <span className="text-capitalize">{`${String(
        laneDetails?.ORIGIN_city
      ).toLocaleLowerCase()} - ${String(
        laneDetails?.DEST_city
      ).toLocaleLowerCase()}`}</span>
    );
  }
  return `Lane ${index + 1}`;
};
