import { Col, Form, Popconfirm, Row, Tabs } from "antd";
import React, { useState, useMemo } from "react";
import Button from "../../components/Buttons/Button";
import IconButton from "../../components/Buttons/IconButton";
import SvgConverter from "../../components/Common/SvgConverter";
import SecondaryLabel from "../../components/Labels/SecondaryLabel";
import Paper from "../../components/Paper/Paper";
import { ReactComponent as ResetIcon } from "../../../assets/icons/reset-icon.svg";
import RateShopFormContainer from "./RateShopFormContainer";
import RateShopResults from "./RateShopResults/RateShopResults";
import { useDispatch, useSelector } from "react-redux";
import {
  getAccessorialsListInSuccess,
  getCarriersByCustomerInSuccess,
  getRateShopResultsForRateShopModuleProgress,
  getTaxListInSuccess,
  setRateShopLaneStopTab,
  setRateShopLaneTab,
  setRateShopStopsDrawer,
} from "../../../redux/RateShop/RateShopActions";
import {
  checkFormValidationStatus,
  triggerNotification,
} from "../../../utilz/utilz";
import { filter } from "lodash";
import { NOTIFICATION_TYPES } from "../../../contants/common";
import {
  DEFAULT_RATE_SHOP_ITEMS_VALUE,
  GLOBAL_RATE_SHOP_FORM_DEFAULT_VALUES,
} from "../../../contants/ShipmentEnums";
import { useLocation } from "react-router-dom";
import { useTranslation } from 'react-i18next';
import { routeFileSelector } from '../../../utilz/i18n';

const RateShopScreen = () => {
  const dispatch = useDispatch();
  const [resetForm] = Form.useForm();
  const [activeTabKey, setActiveTabKey] = useState("1");
  const { carriersByCustomer, selectedCargoTypes, accessorials, taxList } =
    useSelector((state) => state.rateShop);
  const { notificationAPI } = useSelector((state) => state.common);
  const { selectedCompany } = useSelector((state) => state.auth);
  const { pathname: activeTab } = useLocation();
  const namespace = useMemo(() => routeFileSelector(activeTab), [activeTab]);
  const { t } = useTranslation([namespace, 'translation']);

  const handleFormSubmit = async (submittedFormName, { forms }) => {
    try {
      if (submittedFormName === "resetForm") {
        handleReset(forms);
      } else {
        if (submittedFormName === "submitForm") {
          const formList = [
            checkFormValidationStatus(
              forms.globalRateShopForm,
              "globalRateShopForm"
            ),
            checkFormValidationStatus(forms.laneForm, "laneForm"),
            checkFormValidationStatus(forms.itemsForm, "itemsForm"),
          ];
          let formResponse = await Promise.all(formList);
          formResponse = formResponse.reduce((a, b) => {
            return { ...a, ...b };
          }, {});

          formResponse.accessorials = accessorials?.data
            ? filter(accessorials?.data, { isChecked: true })
            : [];

          formResponse.carriers = carriersByCustomer?.data
            ? filter(carriersByCustomer?.data, { isChecked: true })
            : [];

          formResponse.taxes = taxList?.data
            ? filter(taxList?.data, { isChecked: true })
            : [];
          formResponse.lanes = formResponse?.laneForm?.lanes
            ? formResponse?.laneForm?.lanes
            : [];
          formResponse.stops = [];
          if (formResponse?.globalRateShopForm?.cargoType?.includes("LTL")) {
            formResponse.lanes.forEach(({ stops }, laneIndex) => {
              stops = stops ? stops : [];
              formResponse.stops = [
                ...formResponse.stops,
                ...stops.map((row) => ({ ...row, laneId: laneIndex + 1 })),
              ];
            });
          }

          dispatch(getRateShopResultsForRateShopModuleProgress(formResponse));
          setActiveTabKey("2");
        }
      }
    } catch (error) {
      let errorMessages = [];
      if (error?.errorFields?.length > 0) {
        error?.errorFields.forEach(({ errors }) => {
          if (errors) {
            errorMessages = [...errorMessages, ...errors];
          }
        });
      }

      if (error?.formName === "laneForm") {
        if (error?.errorFields?.[0]?.name?.[2]) {
          dispatch(setRateShopStopsDrawer(true));
          dispatch(
            setRateShopLaneStopTab(String(error?.errorFields?.[0]?.name?.[1]))
          );
        }
      }
      if (errorMessages?.length > 0) {
        triggerNotification(notificationAPI, {
          title: errorMessages.map((val, key) => (
            <span key={key}>
              {val}
              <br />
            </span>
          )),
          type: NOTIFICATION_TYPES.ERROR,
        });
      }
    }
  };

  const handleReset = (forms) => {
    forms.globalRateShopForm.setFieldsValue({
      ...GLOBAL_RATE_SHOP_FORM_DEFAULT_VALUES,
      customerKey: selectedCompany?.companyId,
    });
    forms.itemsForm.setFieldsValue({
      items: [DEFAULT_RATE_SHOP_ITEMS_VALUE],
    });
    forms.laneForm.setFieldsValue({
      lanes: [{ stops: [] }],
    });
    dispatch(setRateShopLaneTab("0"));
    dispatch(
      getCarriersByCustomerInSuccess({
        data: carriersByCustomer?.data
          ? carriersByCustomer?.data.map((row) => ({
              ...row,
              isChecked: false,
            }))
          : [],
      })
    );
    dispatch(
      getAccessorialsListInSuccess({
        data: accessorials?.data
          ? accessorials?.data.map((row) => ({
              ...row,
              isChecked: row?.codeId === "FUE",
            }))
          : [],
      })
    );
    dispatch(
      getTaxListInSuccess({
        data: taxList?.data
          ? taxList?.data.map((row) => ({
              ...row,
              isChecked: row?.codeId === "VAT",
            }))
          : [],
      })
    );
  };

  return (
    <Form.Provider onFormFinish={handleFormSubmit}>
      <div className="h-100 d-flex flex-direction-column">
        <div className="mb-15">
          <Paper className={"p-15 border-radius-bottom-none"}>
            <Row align="middle" justify="space-between" gutter={30}>
              <Col className="flex-1">
                <Tabs
                  className="page-level-tab-container tabs-margin-none"
                  onChange={(key) => setActiveTabKey(key)}
                  activeKey={activeTabKey}
                >
                  <Tabs.TabPane key={"1"} tab={t('translation:default.buttons.rateShop')}></Tabs.TabPane>
                  <Tabs.TabPane
                    key={"2"}
                    tab={t('translation:default.buttons.rateShop')}
                  ></Tabs.TabPane>
                </Tabs>
              </Col>
              <Col>
                <div>
                  <Row gutter={15} align={"middle"}>
                    <Col>
                      <Form name="submitForm">
                        <Button buttonType="submit">
                         {t('translation:rateShop.buttons.viewRateShopResults')}
                        </Button>
                      </Form>
                    </Col>
                    <Col>
                      <Button
                        onClick={() => dispatch(setRateShopStopsDrawer(true))}
                        disabled={!selectedCargoTypes?.includes("LTL")}
                      >
                         {t('translation:rateShop.buttons.stops')}
                      </Button>
                    </Col>
                    <Col>
                      <Form name="resetForm" form={resetForm}>
                        <Popconfirm
                          placement="topRight"
                          title=  {t('translation:default.fields.stops')}
                          onConfirm={() => resetForm.submit()}
                          okText={t('translation:default.fields.reset')}
                          cancelText={t('translation:default.options.no')}
                          overlayClassName="delete-popup-container"
                          okType={"danger"}
                        >
                          <IconButton buttonType="submit">
                            <Row gutter={6} align={"middle"}>
                              <Col className="lh-0">
                                <SvgConverter Icon={ResetIcon} color={"dark"} />
                              </Col>
                              <Col className="lh-0">
                                <SecondaryLabel className="f-14 fw-400">
                                  {t('translation:default.fields.reset')}
                                </SecondaryLabel>
                              </Col>
                            </Row>
                          </IconButton>
                        </Popconfirm>
                      </Form>
                    </Col>
                  </Row>
                </div>
              </Col>
            </Row>
          </Paper>
        </div>
        <div className="flex-1 overflow-hidden">
          <div className={`h-100 overflow-hidden`}>
            <div
              className={`h-100 overflow-hidden ${
                activeTabKey === "1" ? "" : "d-none"
              }`}
            >
              <RateShopFormContainer />
            </div>
            <div
              className={`h-100 overflow-hidden ${
                activeTabKey === "2" ? "" : "d-none"
              }`}
            >
              <RateShopResults />
            </div>
          </div>
        </div>
      </div>
    </Form.Provider>
  );
};

export default RateShopScreen;
