import { Collapse, Form } from "antd";
import React, { useEffect, useMemo } from "react";
import { useDispatch, useSelector } from "react-redux";
import { GLOBAL_RATE_SHOP_FORM_DEFAULT_VALUES } from "../../../../contants/ShipmentEnums";
import {
  setGlobalRateShopTab,
  setSelectedRateShopCargoType,
} from "../../../../redux/RateShop/RateShopActions";
import GeneralRateShopFields from "./GeneralRateShopFields";
import OptionalRateShopFields from "./OptionalRateShopFields";
import { useLocation } from "react-router-dom";
import { routeFileSelector } from "../../../../utilz/i18n";
import { useTranslation } from "react-i18next";
const { Panel } = Collapse;

const GlobalRateShopTabs = () => {
  const { globalRateShopTab } = useSelector((state) => state.rateShop);
  const { selectedCompany } = useSelector((state) => state.auth);
  const [form] = Form.useForm();
  const dispatch = useDispatch();
  const { pathname: activeTab } = useLocation();
  const namespace = useMemo(() => routeFileSelector(activeTab), [activeTab]);
  const { t } = useTranslation([namespace, 'translation']);

  useEffect(() => {
    form.setFieldsValue({ customerKey: selectedCompany?.companyId });
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [selectedCompany?.companyId]);

  const handleTabClick = (keys) => {
    dispatch(setGlobalRateShopTab(keys));
  };

  const cargoType = Form.useWatch("cargoType", form);

  useEffect(() => {
    dispatch(setSelectedRateShopCargoType(cargoType ? cargoType : []));
  }, [cargoType]);
  return (
    <Form
      name="globalRateShopForm"
      form={form}
      className="h-100 custom-scroll-bar py-15"
      initialValues={GLOBAL_RATE_SHOP_FORM_DEFAULT_VALUES}
    >
      <Collapse
        ghost
        className="filter-collapse-container px-16"
        activeKey={globalRateShopTab}
        onChange={handleTabClick}
      >
        <Panel header={t('translation:rateShop.rateShop.general')} key="1">
          <GeneralRateShopFields form={form} />
        </Panel>
        <Panel header={t('translation:rateShop.rateShop.optional')} key="2">
          <OptionalRateShopFields form={form} />
        </Panel>
      </Collapse>
    </Form>
  );
};

export default GlobalRateShopTabs;
