import React, { useMemo } from "react";
import { LINER_CODES_OPTIONS } from "../../../../contants/common";
import FormInput from "../../../components/Inputs/FormInput";
import FormSelectBox from "../../../components/Inputs/FormSelectBox";
import ProductSelector from "../../../components/Selectors/AsynchSearch/ProductSelector";
import EquipmentTypeSelector from "../../../components/Selectors/EquipmentTypeSelector";
import LinerCodesSelector from "../../../components/Selectors/LinerCodesSelector";
import { useLocation } from "react-router-dom";
import { useTranslation } from "react-i18next";
import { routeFileSelector } from "../../../../utilz/i18n";

const OptionalRateShopFields = ({ form }) => {
  const { pathname: activeTab } = useLocation();
  const namespace = useMemo(() => routeFileSelector(activeTab), [activeTab]);
  const { t } = useTranslation([namespace, "translation"]);

  return (
    <div className="mt-10">
      <EquipmentTypeSelector name={"trailerKey"} />
      <ProductSelector
        label={t("translation:default.fields.product.label")}
        placeholder={t("translation:default.fields.product.label")}
        margin={"mb-15"}
        form={form}
        name={"productCode"}
      />
      <FormInput
        label={t("translation:rateShop.rateShop.account.label")}
        editMode={true}
        name={"accountNumber"}
        maxLength={12}
      />
      <FormInput
        label={t("translation:rateShop.rateShop.declaredValue.label")}
        editMode={true}
        name={"declaredValue"}
      />
      <FormInput
        label={t("translation:rateShop.rateShop.codValue.label")}
        editMode={true}
        name={"CODValue"}
      />
      <FormInput
        label={t("translation:rateShop.rateShop.ldmValue.label")}
        editMode={true}
        name={"LDMValue"}
      />
      <LinerCodesSelector name={"linerCode"} margin={"mb-15"} />
      <FormSelectBox
        name={"linerType"}
        options={LINER_CODES_OPTIONS}
        label={t("translation:default.fields.linerType.label")}
        placeholder={t("translation:default.fields.linerType.placeholder")}
        margin={"m-0"}
      />
    </div>
  );
};

export default OptionalRateShopFields;
