import { Col, Row } from 'antd';
import React, { useMemo } from "react";
import FilterCard from '../../../components/Cards/FilterCard';
import SvgConverter from '../../../components/Common/SvgConverter';
import { ReactComponent as LocationIcon } from '../../../../assets/icons/location-pin-green.svg';
import SecondaryLabel from '../../../components/Labels/SecondaryLabel';
import CountrySelector from '../../../components/Selectors/CountrySelector';
import StateSelector from '../../../components/Selectors/StateSelector';
import PostalCodeInput from '../../../components/Selectors/PostalCodeInput';
import CitySelector from '../../../components/Selectors/CitySelector';
import PortTypeSelector from '../../../components/Selectors/PortTypeSelector';
import PortNameSelector from '../../../components/Selectors/PortNameSelector';
import FormSelectBox from '../../../components/Inputs/FormSelectBox';
import ShipmentBranchSelector from '../../../components/Selectors/ShipmentBranchSelector';
import { useSelector } from 'react-redux';
import HiddenFormInput from '../../../components/Inputs/HiddenFormInput';
import { useLocation } from "react-router-dom";
import { useTranslation } from 'react-i18next';
import { routeFileSelector } from '../../../../utilz/i18n';

const LaneForm = ({
  laneType,
  name,
  data = {},
  handleDataChanges = () => {},
}) => {
  const { pathname: activeTab } = useLocation();
  const namespace = useMemo(() => routeFileSelector(activeTab), [activeTab]);
  const { t } = useTranslation([namespace, 'translation']);
  const { selectedCompany } = useSelector((state) => state.auth);
  const name_prefix = useMemo(
    () => (laneType === '1' ? 'ORIGIN' : 'DEST'),
    [laneType]
  );

  const handleZipDetails = (postalData) => {
    handleDataChanges(
      {
        ...data,
        [`${name_prefix}_stateCode`]: postalData?.state,
        [`${name_prefix}_city`]: postalData?.city,
        [`${name_prefix}_postalCode`]: postalData?.zip,
        [`${name_prefix}_countryCode`]: postalData?.countryCode,
      },
      name
    );
  };

  const handlePortDetails = (portData) => {
    handleDataChanges(
      {
        ...data,
        [`${name_prefix}_portName`]: portData?.portName,
        [`${name_prefix}_portRecKey`]: portData?.portRecKey,
        [`${name_prefix}_portCode`]: portData?.portCode,
        [`${name_prefix}_portType`]: portData?.portType,
      },
      name
    );
  };

  return (
    <FilterCard
      shadow={false}
      showBorder={true}
      title={
        <Row gutter={10}>
          <Col className='lh-0'>
            <SvgConverter
              Icon={LocationIcon}
              color={laneType === '1' ? 'success' : 'danger-3'}
            />
          </Col>
          <Col>
            <SecondaryLabel
              className={`f-14 fw-500 text-${
                laneType === '1' ? 'success' : 'danger-3'
              }`}
            >
              {laneType === '1' ? t('translation:default.fields.origin') : t('translation:default.fields.destination')}
            </SecondaryLabel>
          </Col>
        </Row>
      }
    >
      <div className='px-15 pb-15 py-10'>
        <Row gutter={15}>
          <Col sm={12}>
            <CountrySelector
              margin={'mb-12'}
              name={[name, `${name_prefix}_countryCode`]}
              rules={[{ required: true, message: t('translation:default.fields.country.message') }]}
            />
            <PostalCodeInput
              margin={'mb-12'}
              editMode={true}
              name={[name, `${name_prefix}_postalCode`]}
              rules={[
                { required: true, message: t('translation:default.fields.postalCode.message') },
              ]}
              countryCode={data[`${name_prefix}_countryCode`]}
              handleZipDetails={handleZipDetails}
            />
            <StateSelector
              margin={'mb-12'}
              name={[name, `${name_prefix}_stateCode`]}
              countryCode={data[`${name_prefix}_countryCode`]}
            />
            <CitySelector margin={'m-0'} name={[name, `${name_prefix}_city`]} />
          </Col>
          <Col sm={12}>
            <PortTypeSelector
              margin={'mb-12'}
              name={[name, `${name_prefix}_portType`]}
            />
            <PortNameSelector
              portType={data[`${name_prefix}_portType`]}
              countryCode={data[`${name_prefix}_countryCode`]}
              margin={'mb-12'}
              label={t('translation:rateShop.rateShop.port')}
              name={[name, `${name_prefix}_portName`]}
              handlePortDetails={handlePortDetails}
              editMode={true}
            />
            <FormSelectBox
              options={[]}
              margin={'mb-12'}
              label={`${t('translation:rateShop.rateShop.port')}*`}
              name={[name, `${name_prefix}_locationId`]}
            />
            <ShipmentBranchSelector
              customerId={selectedCompany?.companyId}
              label={t('translation:default.fields.branch')}
              showAll={true}
              margin={'m-0'}
              name={[name, `${name_prefix}_branchId`]}
            />
          </Col>
        </Row>
      </div>
      <HiddenFormInput name={[name, `${name_prefix}_portCode`]} />
      <HiddenFormInput name={[name, `${name_prefix}_portRecKey`]} />
    </FilterCard>
  );
};

export default LaneForm;
