import React, { useMemo } from "react";
import usePricingBasis from "../../../redux/hooks/usePricingBasis";
import FormSelectBox from "../Inputs/FormSelectBox";
import { useLocation } from "react-router-dom";
import { routeFileSelector } from "../../../utilz/i18n";
import { useTranslation } from "react-i18next";
import i18n from 'i18next';

const PricingBasisSelector = ({
  placeholder = i18n.t('translation:default.fields.pricingBasis.placeholder'),
  label = i18n.t('translation:default.fields.pricingBasis.label'),
  rules = [],
  name,
  editMode,
  value,
  tooltip,
  margin = "mb-15",
  mode = "",
}) => {
  const pricingBasis = usePricingBasis();
  const { pathname: activeTab } = useLocation();
  const namespace = useMemo(() => routeFileSelector(activeTab), [activeTab]);
  const { t } = useTranslation([namespace, 'translation']);
  return (
    <FormSelectBox
      loading={pricingBasis === undefined}
      options={[
        {
          label: t('translation:default.options.all'),
          value: "**",
        },
        ...(pricingBasis
          ? pricingBasis.map((row) => ({
              label: row.codeDesc,
              value: row.codeId,
            }))
          : []),
      ]}
      placeholder={placeholder}
      label={label}
      margin={margin}
      rules={rules}
      name={name}
      editMode={editMode}
      value={value}
      tooltip={tooltip}
      mode={mode}
    />
  );
};

export default PricingBasisSelector;
