import SvgConverter from "./SvgConverter";
import { ReactComponent as BellIcon } from "../../../assets/icons/bell.svg";

const GenerateBellFlag = ({ value = "" }) => {
  return (
    <SvgConverter
      Icon={BellIcon}
      color={value === "Y" ? "success" : "danger"}
    />
  );
};

export default GenerateBellFlag;
