import { Col, Divider, Row } from "antd";
import React, { useMemo } from "react";
import FooterFieldCard from "./FooterFieldCard";
import ProductDetailsIndividual from "./ProductDetailsIndividual";
import RateShopIndividualDetailsLayout from "./RateShopIndividualDetailsLayout";
import { useLocation } from "react-router-dom";
import { useTranslation } from 'react-i18next';
import { routeFileSelector } from '../../../../../utilz/i18n';

const ProductDetails = ({
  items,
  note,
  totalDiscount,
  totalItemsCharge,
  grossCharge,
  deficitWeight,
  deficitRate,
  totalDeficitCharge,
  ABSTotalCharge,
}) => {
  const { pathname: activeTab } = useLocation();
  const namespace = useMemo(() => routeFileSelector(activeTab), [activeTab]);
  const { t } = useTranslation([namespace, 'translation']);
  return (
    <RateShopIndividualDetailsLayout>
      <RateShopIndividualDetailsLayout.ScrollContainer>
        {items.map((item, key) => (
          <ProductDetailsIndividual key={key} index={key} data={item} />
        ))}
      </RateShopIndividualDetailsLayout.ScrollContainer>
      <RateShopIndividualDetailsLayout.FooterContainer>
        <Row justify={"space-around pt-10 px-15"} align={"middle"}>
          <Col className="w-59">
            <label className="fw-12 text-white fw-400">{`${t('translation:rateShop.rateShopResult.note')} :}`}</label>
            <p className="fw-600 f-14 text-white">{note}</p>
          </Col>
          <Col className="border-right-border height-33px"></Col>
          <Col className="w-19">
            <FooterFieldCard title={t('translation:rateShop.rateShopResult.deficitWeight')} value={deficitWeight} />
          </Col>
          <Col className="border-right-border height-33px"></Col>
          <Col className="w-19">
            <FooterFieldCard title={t('translation:rateShop.rateShopResult.deficitRate')}  value={deficitRate} />
          </Col>
        </Row>
        <Divider className="my-10 border-top-border" />
        <div className="px-15 pb-10">
          <Row justify={"space-around"} align={"middle"}>
            <Col className="w-19">
              <FooterFieldCard
                title={t('translation:rateShop.rateShopResult.itemsTotalCharge')}
                value={totalItemsCharge}
              />
            </Col>
            <Col className="border-right-border height-33px"></Col>
            <Col className="w-19">
              <FooterFieldCard
                title={t('translation:rateShop.rateShopResult.deficitCharge')}
                value={totalDeficitCharge}
              />
            </Col>
            <Col className="border-right-border height-33px"></Col>
            <Col className="w-19">
              <FooterFieldCard title={t('translation:rateShop.rateShopResult.grossCharge')} value={grossCharge} />
            </Col>
            <Col className="border-right-border height-33px"></Col>
            <Col className="w-19">
              <FooterFieldCard title={t('translation:rateShop.rateShopResult.totalDiscount')}  value={totalDiscount} />
            </Col>
            <Col className="border-right-border height-33px"></Col>
            <Col className="w-19">
              <FooterFieldCard title={t('translation:rateShop.rateShopResult.baseCharge')} value={ABSTotalCharge} />
            </Col>
          </Row>
        </div>
      </RateShopIndividualDetailsLayout.FooterContainer>
    </RateShopIndividualDetailsLayout>
  );
};

export default ProductDetails;
