import React from "react";
import useLinerCodes from "../../../redux/hooks/useLinerCodes";
import FormSelectBox from "../Inputs/FormSelectBox";
import i18n from 'i18next';

const LinerCodesSelector = ({
  placeholder = i18n.t('translation:default.fields.linerType.placeholder'),
  label =  i18n.t('translation:default.fields.linerType.label'),
  rules = [],
  name,
  editMode,
  value,
  tooltip,
  margin = "mb-15",
  mode = "",
}) => {
  const linerTypes = useLinerCodes();

  return (
    <FormSelectBox
      loading={linerTypes === undefined}
      options={
        linerTypes
          ? linerTypes.map((row) => ({
              label: row.codeDesc,
              value: row.codeId,
            }))
          : []
      }
      placeholder={placeholder}
      label={label}
      margin={margin}
      rules={rules}
      name={name}
      editMode={editMode}
      value={value}
      tooltip={tooltip}
      mode={mode}
    />
  );
};

export default LinerCodesSelector;
