import { Col, Divider, Row } from "antd";
import { sumBy } from "lodash";
import React, { useState, useMemo } from "react";
import { useDispatch, useSelector } from "react-redux";
import { setSelectedRateShopCarrierRate } from "../../../../redux/RateShop/RateShopActions";
import FormLabel from "../../../components/Labels/FormLabel";
import SecondaryLabel from "../../../components/Labels/SecondaryLabel";
import Drawer from "../../../components/Modals/Drawer";
import Tab from "../../../components/Tabs/Tab";
import AccessorialDetails from "./IndividualDetails/AccessorialDetails";
import ProductDetails from "./IndividualDetails/ProductDetails";
import TaxDetails from "./IndividualDetails/TaxDetails";
import { useLocation } from "react-router-dom";
import { useTranslation } from 'react-i18next';
import { routeFileSelector } from '../../../../utilz/i18n';

const RateShopResultDetailsDrawer = () => {
  const dispatch = useDispatch();
  const { selectedCarrierRate } = useSelector((state) => state.rateShop);
  const [tab, setTab] = useState("1");
  const { pathname: activeTab } = useLocation();
  const namespace = useMemo(() => routeFileSelector(activeTab), [activeTab]);
  const { t } = useTranslation([namespace, 'translation']);

  return (
    <Drawer
      visible={Boolean(selectedCarrierRate)}
      onClose={() => dispatch(setSelectedRateShopCarrierRate(false))}
      headerContents={
        <Row gutter={30}>
          <Col>
            <label className="text-white f-12 fw-400 lh-0">
              {t('translation:default.fields.transportationProvider.label')}
            </label>
            <br />
            <SecondaryLabel className="text-white fw-600 f-16">
              {selectedCarrierRate?.carrierName}
            </SecondaryLabel>
          </Col>
          <Col>
            <label className="text-white f-12 fw-400 lh-0"> {t('translation:default.fields.currency.label')}</label>
            <br />
            <SecondaryLabel className="text-white fw-600 f-16">
              {selectedCarrierRate?.currencyCode}
            </SecondaryLabel>
          </Col>
          <Col>
            <div className="cursor-pointer" onClick={() => setTab("1")}>
              <label className="text-white cursor-pointer f-12 fw-400 lh-0">
                {t('translation:rateShop.rateShopResult.baseCharge')}
              </label>
              <SecondaryLabel className="text-white fw-600 f-16 d-flex w-100 justify-content-end">
                {selectedCarrierRate?.ABSTotalCharge}
              </SecondaryLabel>
            </div>
          </Col>
          <Col>
            <div className="cursor-pointer" onClick={() => setTab("2")}>
              <label className="text-white cursor-pointer f-12 fw-400 lh-0">
                 {t('translation:rateShop.totalAccessorialCharge')}
              </label>
              <SecondaryLabel className="text-white fw-600 f-16 d-flex w-100 justify-content-end">
                {sumBy(
                  selectedCarrierRate?.accessorials,
                  ({ auditedChargeAmount }) => auditedChargeAmount
                )}
              </SecondaryLabel>
            </div>
          </Col>
          <Col>
            <div className="cursor-pointer" onClick={() => setTab("3")}>
              <label className="text-white cursor-pointer f-12 fw-400 lh-0">
                {t('translation:rateShop.rateShopResult.totalTaxCharge')}
              </label>
              <SecondaryLabel className="text-white fw-600 f-16 d-flex w-100 justify-content-end">
                {sumBy(
                  selectedCarrierRate?.taxes,
                  ({ auditedChargeAmount }) => auditedChargeAmount
                )}
              </SecondaryLabel>
            </div>
          </Col>
          <Col>
            <label className="text-white f-12 fw-400 lh-0">
               {t('translation:rateShop.rateShopResult.totalShipmentCost')}
            </label>
            <br />
            <SecondaryLabel className="text-white fw-600 f-16 d-flex w-100 justify-content-end">
              {selectedCarrierRate?.totalShipmentCost}
            </SecondaryLabel>
          </Col>
        </Row>
      }
      width={"70%"}
    >
      <div className="h-100 overflow-hidden">
        <Row className="h-100 overflow-hidden flex-direction-column flex-nowrap">
          <Col className="px-15 pt-15">
            <div>
              <Row gutter={15}>
                <Col sm={24} md={4}>
                  <div className="mb-15">
                    <FormLabel>{t('translation:rateShop.rateShopResult.service')}</FormLabel>
                    <br />
                    <SecondaryLabel className="fw-600 f-14">
                      {selectedCarrierRate?.serviceDesc}
                    </SecondaryLabel>
                  </div>
                  <div className="mb-15">
                    <FormLabel>{t('translation:rateShop.rateShopResult.equipment')}</FormLabel>
                    <br />
                    <SecondaryLabel className="fw-600 f-14">
                      {selectedCarrierRate?.equipmentTypeDetail}
                    </SecondaryLabel>
                  </div>
                  <div className="mb-15">
                    <FormLabel>{t('translation:rateShop.rateShopResult.distance')}</FormLabel>
                    <br />
                    <SecondaryLabel className="fw-600 f-14">
                      {selectedCarrierRate?.mileage}{" "}
                      <span className="f-12 fw-400">{t('translation:rateShop.rateShopResult.miles')}</span> /{" "}
                      {selectedCarrierRate?.mileage * 1.6}{" "}
                      <span className="f-12 fw-400">{t('translation:rateShop.rateShopResult.kms')}</span>
                    </SecondaryLabel>
                  </div>
                </Col>
                <Col sm={24} md={4}>
                  <div className="mb-15">
                    <FormLabel>{t('translation:rateShop.rateShopResult.tariffName')}</FormLabel>
                    <br />
                    <SecondaryLabel className="fw-600 f-14">
                      {selectedCarrierRate?.tariffName}{" "}
                      {selectedCarrierRate?.tariffRateDate}
                    </SecondaryLabel>
                  </div>
                  <div className="mb-15">
                    <FormLabel>{t('translation:default.fields.accountNumber.label')}</FormLabel>
                    <br />
                    <SecondaryLabel className="fw-600 f-14">
                      {selectedCarrierRate?.accountNumber}
                    </SecondaryLabel>
                  </div>
                </Col>
                <Col sm={24} md={4}>
                  <div className="mb-15">
                    <FormLabel>{t('translation:rateShop.rateShopResult.pricingEffectiveDate')}</FormLabel>
                    <br />
                    <SecondaryLabel className="fw-600 f-14">
                      {selectedCarrierRate?.pricingEffectiveFrom} {t('translation:rateShop.rateShopResult.to')}{" "}
                      {selectedCarrierRate?.pricingEffectiveTo}
                    </SecondaryLabel>
                  </div>
                  <div className="mb-15">
                    <FormLabel>{t('translation:rateShop.rateShopResult.tariffEffectiveDate')}</FormLabel>
                    <br />
                    <SecondaryLabel className="fw-600 f-14">
                      {selectedCarrierRate?.tariffEffectiveFrom} {t('translation:rateShop.rateShopResult.to')}{" "}
                      {selectedCarrierRate?.tariffEffectiveTo}
                    </SecondaryLabel>
                  </div>
                </Col>
                <Col sm={24} md={4}>
                  <div className="mb-15">
                    <FormLabel>{t('translation:rateShop.rateShopResult.pricingKey')}</FormLabel>
                    <br />
                    <SecondaryLabel className="fw-600 f-14">
                      {selectedCarrierRate?.pricingDetailKey}
                    </SecondaryLabel>
                  </div>
                  <div className="mb-15">
                    <FormLabel>{t('translation:rateShop.rateShopResult.tariffKey')}</FormLabel>
                    <br />
                    <SecondaryLabel className="fw-600 f-14">
                      {selectedCarrierRate?.tariffKey}
                    </SecondaryLabel>
                  </div>
                </Col>
                <Col sm={24} md={4}>
                  <div className="mb-15">
                    <FormLabel>{t('translation:rateShop.rateShopResult.liner')}</FormLabel>
                    <br />
                    <SecondaryLabel className="fw-600 f-14">
                      {selectedCarrierRate?.liner}
                    </SecondaryLabel>
                  </div>
                  <div className="mb-15">
                    <FormLabel>{t('translation:default.fields.linerType.label')}</FormLabel>
                    <br />
                    <SecondaryLabel className="fw-600 f-14">
                      {selectedCarrierRate?.lineType}
                    </SecondaryLabel>
                  </div>
                </Col>
                <Col sm={24} md={4}>
                  <div className="mb-15">
                    <FormLabel>{t('translation:rateShop.rateShopResult.laneCode')}</FormLabel>
                    <br />
                    <SecondaryLabel className="fw-600 f-14">
                      {selectedCarrierRate?.laneCode}
                    </SecondaryLabel>
                  </div>
                  <div className="mb-15">
                    <FormLabel>{t('translation:rateShop.rateShopResult.laneAlias')}</FormLabel>
                    <br />
                    <SecondaryLabel className="fw-600 f-14">
                      {selectedCarrierRate?.laneKey}
                    </SecondaryLabel>
                  </div>
                </Col>
              </Row>
            </div>
          </Col>
          <Col>
            <Divider className="border-top-border m-0" />
          </Col>
          <Col className="flex-1 ">
            <Tab
              activeKey={tab}
              onChange={(key) => setTab(key)}
              contents={[
                {
                  title: t('translation:rateShop.rateShopResult.rateShopResult'),
                  content: (
                    <ProductDetails
                      items={
                        selectedCarrierRate?.items
                          ? selectedCarrierRate?.items
                          : []
                      }
                      note={selectedCarrierRate?.note}
                      totalDiscount={selectedCarrierRate?.totalDiscount}
                      totalItemsCharge={sumBy(
                        selectedCarrierRate?.items,
                        ({ rateCharge }) => rateCharge
                      )}
                      grossCharge={selectedCarrierRate?.grossCharge}
                      deficitWeight={selectedCarrierRate?.deficitWeight}
                      deficitRate={selectedCarrierRate?.deficitRate}
                      totalDeficitCharge={
                        selectedCarrierRate?.totalDeficitCharge
                      }
                      ABSTotalCharge={selectedCarrierRate?.ABSTotalCharge}
                    />
                  ),
                  contentKey: "1",
                },
                {
                  title: t('translation:rateShop.rateShopResult.accessorials'),
                  content: (
                    <AccessorialDetails
                      accessorials={
                        selectedCarrierRate?.accessorials
                          ? selectedCarrierRate?.accessorials
                          : []
                      }
                    />
                  ),
                  contentKey: "2",
                },
                {
                  title: t('translation:rateShop.rateShopResult.taxes'),
                  content: (
                    <TaxDetails
                      taxes={
                        selectedCarrierRate?.taxes
                          ? selectedCarrierRate?.taxes
                          : []
                      }
                    />
                  ),
                  contentKey: "3",
                },
              ]}
              defaultActiveKey={"1"}
              //   activeKey={activeKey}
            />
          </Col>
        </Row>
      </div>
    </Drawer>
  );
};

export default RateShopResultDetailsDrawer;
