import { Col, Popconfirm, Row } from "antd";
import React, { useEffect, useMemo } from "react";
import IconButton from "../../../components/Buttons/IconButton";
import FilterCard from "../../../components/Cards/FilterCard";
import SvgConverter from "../../../components/Common/SvgConverter";
import FormCheckBox from "../../../components/Inputs/FormCheckBox";
import FormInput from "../../../components/Inputs/FormInput";
import ShipmentProductClassSelector from "../../../components/Selectors/ShipmentProductClassSelector";
import { ReactComponent as CloseIcon } from "../../../../assets/icons/close-white.svg";
import { calculateCube } from "../../../../utilz/utilz";
import { useLocation } from "react-router-dom";
import { useTranslation } from 'react-i18next';
import { routeFileSelector } from '../../../../utilz/i18n';

const ItemCard = ({
  index,
  removeItem,
  name,
  uomDetails = {},
  data = {},
  handleCubeChange = () => {},
}) => {
  const { pathname: activeTab } = useLocation();
  const namespace = useMemo(() => routeFileSelector(activeTab), [activeTab]);
  const { t } = useTranslation([namespace, 'translation']);

  useEffect(() => {
    if (uomDetails?.dimUom) {
      const cube = calculateCube(
        data?.length,
        data?.width,
        data?.height,
        data?.billQuantity,
        String(uomDetails?.dimUom).toUpperCase()
      );
      handleCubeChange(cube, index);
    }
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [
    uomDetails?.dimUom,
    data?.length,
    data?.width,
    data?.height,
    data?.billQuantity,
  ]);

  return (
    <FilterCard
      showBorder
      shadow={false}
      className={"mb-15"}
      customHeader={
        <div className="w-100">
          <Row justify={"space-between"} align={"middle"} gutter={15}>
            <Col>
              <FormCheckBox
                checkBoxClass="normal-checkbox"
                className={"m-0"}
                label={`Item ${index + 1}`}
                name={[name, "selected"]}
              />
            </Col>
            <Col>
              <Popconfirm
                placement="topRight"
                title={t('translation:default.popConfirm.title')}
                onConfirm={() => removeItem(name)}
                okText={t('translation:default.buttons.delete')}
                cancelText={t('translation:default.options.no')}
                overlayClassName="delete-popup-container"
                okType={"danger"}
              >
                <IconButton>
                  <SvgConverter Icon={CloseIcon} color={"header"} />
                </IconButton>
              </Popconfirm>
            </Col>
          </Row>
        </div>
      }
    >
      <div className="px-15 pt-12">
        <Row gutter={15}>
          <Col sm={6}>
            <FormInput
              label={t('translation:rateShop.rateShop.numberOfPieces.label')}
              inputType={"number"}
              editMode={true}
              margin={"mb-15"}
              name={[name, "numberOfPieces"]}
              rules={[
                () => ({
                  validator(_, value) {
                    if (value >= 0) return Promise.resolve();
                    return Promise.reject(
                      new Error(t('translation:rateShop.rateShop.numberOfPieces.message'))
                    );
                  },
                }),
              ]}
            />
            <FormInput
              label={`${t('translation:default.fields.length')} (${uomDetails.dimUom})`}
              inputType={"number"}
              editMode={true}
              margin={"mb-15"}
              name={[name, "length"]}
              rules={[
                () => ({
                  validator(_, value) {
                    if (isNaN(parseInt(value)) && String(value).length > 0)
                      return Promise.reject(
                        new Error(t('translation:default.notification.numeric'))
                      );
                    if (value > 0 || parseInt(value) === 0)
                      return Promise.resolve();
                    if (value < 0)
                      return Promise.reject(
                        new Error(t('translation:rateShop.rateShop.length.errorLength'))
                      );
                  },
                }),
              ]}
            />
          </Col>
          <Col sm={6}>
            <FormInput
              label={`${t('translation:default.fields.weight')} (${uomDetails.weightUom})`}
              inputType={"number"}
              editMode={true}
              margin={"mb-15"}
              name={[name, "billedWeight"]}
              rules={[
                { required: true, message: t('translation:rateShop.rateShop.weight.message')},
                () => ({
                  validator(_, value) {
                    if (isNaN(parseInt(value)) && String(value).length > 0)
                      return Promise.reject(
                        new Error(t('translation:default.notification.numeric'))
                      );
                    if (value < 0)
                      return Promise.reject(
                        new Error(t('translation:rateShop.rateShop.weight.errorWeight'))
                      );
                    if (value > 0) return Promise.resolve();
                    if (value == 0)
                      return Promise.reject(
                        new Error(t('translation:rateShop.rateShop.weight.errorZero'))
                      );
                  },
                }),
              ]}
            />{" "}
            <FormInput
              label={`${t('translation:default.fields.height')} (${uomDetails.dimUom})`}
              inputType={"number"}
              editMode={true}
              margin={"mb-15"}
              name={[name, "height"]}
              rules={[
                () => ({
                  validator(_, value) {
                    if (isNaN(parseInt(value)) && String(value).length > 0)
                      return Promise.reject(
                        new Error(t('translation:default.notification.numeric'))
                      );
                    if (value > 0 || parseInt(value) === 0)
                      return Promise.resolve();
                    if (value < 0)
                      return Promise.reject(
                        new Error(t('translation:rateShop.rateShop.height.errorNegative'))
                      );
                  },
                }),
              ]}
            />
          </Col>
          <Col sm={6}>
            <FormInput
              label={t('translation:rateShop.rateShop.billedQuantity.label')}
              inputType={"number"}
              editMode={true}
              margin={"mb-15"}
              name={[name, "billQuantity"]}
              rules={[
                () => ({
                  validator(_, value) {
                    if (isNaN(parseInt(value)) && String(value).length > 0)
                      return Promise.reject(
                        new Error(t('translation:default.notification.numeric'))
                      );
                    if (value > 0 || parseInt(value) === 0)
                      return Promise.resolve();
                    if (value < 0)
                      return Promise.reject(
                        new Error(t('translation:rateShop.rateShop.billedQuantity.errorNegative'))
                      );
                  },
                }),
              ]}
            />{" "}
            <FormInput
              label={`${t('translation:default.fields.width') } (${uomDetails.dimUom})`}
              inputType={"number"}
              editMode={true}
              margin={"mb-15"}
              name={[name, "width"]}
              rules={[
                () => ({
                  validator(_, value) {
                    if (isNaN(parseInt(value)) && String(value).length > 0)
                      return Promise.reject(
                        new Error(t('translation:default.notification.numeric'))
                      );
                    if (value > 0 || parseInt(value) === 0)
                      return Promise.resolve();
                    if (value < 0)
                      return Promise.reject(
                        new Error(t('translation:rateShop.rateShop.width.errorLength'))
                      );
                  },
                }),
              ]}
            />
          </Col>
          <Col sm={6}>
            <ShipmentProductClassSelector
              label={t('translation:rateShop.rateShop.ratingClass.label')}
              editMode={true}
              margin={"mb-15"}
              name={[name, "class"]}
              rules={[
                { required: true, message: t('translation:rateShop.rateShop.ratingClass.message') },
              ]}
            />
            <FormInput
              label={`${t('translation:default.fields.cube.label')} (${
                uomDetails.cubeUom === "m"
                  ? "cbm"
                  : uomDetails.cubeUom === "ft"
                  ? "cu ft"
                  : ""
              })`}
              inputType={"number"}
              editMode={true}
              margin={"mb-15"}
              name={[name, "cube"]}
              rules={[
                () => ({
                  validator(_, value) {
                    if (isNaN(parseInt(value)) && String(value).length > 0)
                      return Promise.reject(
                        new Error(t('translation:default.notification.numeric'))
                      );
                    if (value > 0 || parseInt(value) === 0)
                      return Promise.resolve();
                    if (value < 0)
                      return Promise.reject(
                        new Error(t('translation:default.fields.cube.errorNegative'))
                      );
                  },
                }),
              ]}
            />
          </Col>
        </Row>
      </div>
    </FilterCard>
  );
};

export default ItemCard;
