import { useEffect } from "react";
import { useDispatch, useSelector } from "react-redux";
import { getLinerCodes } from "../lookups/LookupsActions";

const useLinerCodes = () => {
  const { linerList } = useSelector((state) => state.lookups);
  const dispatch = useDispatch();

  useEffect(() => {
    if (!linerList?.isFetched) {
      dispatch(getLinerCodes());
    }
  }, []);

  return linerList?.progressing ? false : linerList?.data;
};

export default useLinerCodes;
