import { Col, Form, Popconfirm, Row } from "antd";
import { cloneDeep, filter, sumBy } from "lodash";
import React, { useEffect, useMemo } from "react";
import { useDispatch, useSelector } from "react-redux";
import { DEFAULT_RATE_SHOP_ITEMS_VALUE } from "../../../../contants/ShipmentEnums";
import { setRateShopItemsSumValues } from "../../../../redux/RateShop/RateShopActions";
import Button from "../../../components/Buttons/Button";
import FilterCard from "../../../components/Cards/FilterCard";
import HiddenFormInput from "../../../components/Inputs/HiddenFormInput";
import PrimaryLabel from "../../../components/Labels/PrimaryLabel";
import ItemCard from "./ItemCard";
import { useLocation } from "react-router-dom";
import { useTranslation } from 'react-i18next';
import { routeFileSelector } from '../../../../utilz/i18n';

const ItemDetails = () => {
  const dispatch = useDispatch();
  const [form] = Form.useForm();
  const itemsFormData = Form.useWatch("items", form);
  const { pathname: activeTab } = useLocation();
  const namespace = useMemo(() => routeFileSelector(activeTab), [activeTab]);
  const { t } = useTranslation([namespace, 'translation']);

  const { globalMeasurementUnit } = useSelector((state) => state.rateShop);

  const uomDetails = useMemo(() => {
    if (String(globalMeasurementUnit).toLocaleLowerCase() === "kg") {
      // Metric
      return {
        weightUom: "kg",
        dimUom: "cm",
        cubeUom: "m",
      };
    } else if (String(globalMeasurementUnit).toLocaleLowerCase() === "lb") {
      // imperial
      return {
        weightUom: "lb",
        dimUom: "in",
        cubeUom: "ft",
      };
    } else {
      return {
        weightUom: "",
        dimUom: "",
        cubeUom: "",
      };
    }
  }, [globalMeasurementUnit]);

  useEffect(() => {
    if (uomDetails) {
      form.setFieldsValue(uomDetails);
    }
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [uomDetails]);

  const { selectedItemsIndex, totalPieces, totalWeight } = useMemo(
    () => ({
      selectedItemsIndex: itemsFormData
        ? filter(itemsFormData, { selected: true }).map((_, i) => i)
        : [],
      totalPieces: sumBy(itemsFormData, ({ numberOfPieces }) =>
        Number(numberOfPieces)
      ),
      totalWeight: sumBy(itemsFormData, ({ billedWeight }) =>
        Number(billedWeight)
      ),
    }),
    [itemsFormData]
  );

  useEffect(() => {
    dispatch(setRateShopItemsSumValues({ totalPieces, totalWeight }));
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [totalPieces, totalWeight]);

  const handleCubeChange = (cube, index) => {
    let items = cloneDeep(itemsFormData);
    if (items && items?.length > 0) {
      items[index].cube = cube;
      form.setFieldValue("items", items);
    }
  };

  return (
    <Form
      name="itemsForm"
      form={form}
      className="h-100 overflow-hidden"
      initialValues={{ items: [DEFAULT_RATE_SHOP_ITEMS_VALUE] }}
    >
      <Form.List name={"items"}>
        {(items, { add: addItem, remove: removeItem }) => (
          <div className="h-100 overflow-hidden d-flex flex-direction-column">
            <FilterCard
              className="background-white flex-1 overflow-hidden d-flex flex-direction-column mb-15"
              customHeader={
                <div className="w-100">
                  <Row justify={"space-between"} align={"middle"}>
                    <Col>
                      <PrimaryLabel className="f-14 text-black">
                        {t('translation:rateShop.rateShop.itemDetails')}
                      </PrimaryLabel>
                    </Col>
                    <Col>
                      <Row gutter={15}>
                        <Col>
                          <Button
                            onClick={() =>
                              addItem(DEFAULT_RATE_SHOP_ITEMS_VALUE)
                            }
                          >
                            {t('translation:rateShop.rateShop.addItem')}
                          </Button>
                        </Col>
                        <Col>
                          <Popconfirm
                            placement="topRight"
                            title={t('translation:default.popConfirm.title')}
                            onConfirm={() => removeItem(selectedItemsIndex)}
                            okText={t('translation:default.buttons.delete')}
                            cancelText={t('translation:default.options.no')}
                            overlayClassName="delete-popup-container"
                            okType={"danger"}
                          >
                            <Button
                              variant="danger"
                              type="default"
                              disabled={selectedItemsIndex?.length === 0}
                            >
                                {t('translation:rateShop.rateShop.deleteItem')}
                            </Button>
                          </Popconfirm>
                        </Col>
                      </Row>
                    </Col>
                  </Row>
                </div>
              }
            >
              <div className="p-15 flex-1 custom-scroll-bar">
                {items?.map(({ name }, index) => (
                  <ItemCard
                    key={name}
                    name={name}
                    data={itemsFormData?.[name] ? itemsFormData?.[name] : {}}
                    index={index}
                    removeItem={removeItem}
                    uomDetails={uomDetails}
                    handleCubeChange={handleCubeChange}
                  />
                ))}
              </div>
            </FilterCard>
          </div>
        )}
      </Form.List>
      <HiddenFormInput name="weightUom" />
      <HiddenFormInput name="dimUom" />
      <HiddenFormInput name="cubeUom" />
    </Form>
  );
};

export default ItemDetails;
