import { Col, Form, Popconfirm, Row } from 'antd';
import React, { useMemo, useEffect } from 'react';
import { SHIPMENT_STOP_TYPES } from '../../../../contants/ShipmentEnums';
import PrimaryLabel from '../../../components/Labels/PrimaryLabel';
import FormCheckBox from '../../../components/Inputs/FormCheckBox';
import StopsFilter from './StopsFilter';
import Button from '../../../components/Buttons/Button';
import StopCard from './StopCard';
import FilterCard from '../../../components/Cards/FilterCard';
import SaveCancelButtons from '../../Shipment/components/SaveCancelButtons';
import EmptyRecords from '../../../components/Common/EmptyRecords';
import { filter, sumBy } from 'lodash';
import SecondaryLabel from '../../../components/Labels/SecondaryLabel';
import { StarFilled } from '@ant-design/icons';
import HiddenFormInput from '../../../components/Inputs/HiddenFormInput';
import { useLocation } from "react-router-dom";
import { useTranslation } from 'react-i18next';
import { routeFileSelector } from '../../../../utilz/i18n';


const StopDetails = ({ form, laneDetails, laneName, itemsSumValues }) => {
  const stopList = useMemo(
    () => (laneDetails?.stops ? laneDetails?.stops : []),
    [laneDetails]
  );
  
  const { pathname: activeTab } = useLocation();
  const namespace = useMemo(() => routeFileSelector(activeTab), [activeTab]);
  const { t } = useTranslation([namespace, 'translation']);

  const selectedStopsIndex = useMemo(() => {
    if (stopList?.length > 0) {
      const indexes = stopList?.map(({ selected }, index) =>
        selected ? index : false
      );
      return filter(indexes, (val) => val !== false);
    }
    return [];
  }, [stopList]);

  const handleSelectAll = (e) => {
    const selectValue = Boolean(e?.target?.checked);
    let laneDetails = form.getFieldValue('lanes');
    laneDetails[laneName].stops = laneDetails[laneName].stops.map((row) => ({
      ...row,
      selected: selectValue,
    }));
    form.setFieldValue('lanes', laneDetails);
  };

  const {
    totalPiecesError,
    totalWeightError,
    totalStopsPieces,
    totalStopsWeight,
  } = useMemo(() => {
    const totalStopsPieces = sumBy(laneDetails?.stops, ({ numberOfPieces }) =>
      Number(numberOfPieces)
    );
    const totalStopsPiecesWithDest =
      (Number(laneDetails?.DEST_numberOfPieces)
        ? Number(laneDetails?.DEST_numberOfPieces)
        : 0) + totalStopsPieces;
    const totalStopsWeight = sumBy(laneDetails?.stops, ({ weight }) =>
      Number(weight)
    );
    const totalStopsWeightWithDest =
      (Number(laneDetails?.DEST_weight)
        ? Number(laneDetails?.DEST_weight)
        : 0) + totalStopsWeight;
    return {
      totalPiecesError:
        itemsSumValues?.totalPieces !== totalStopsPiecesWithDest,
      totalStopsPieces: parseFloat(totalStopsPiecesWithDest).toFixed(4),
      totalWeightError:
        itemsSumValues?.totalWeight !== totalStopsWeightWithDest,
      totalStopsWeight: parseFloat(totalStopsWeightWithDest).toFixed(4),
    };
  }, [itemsSumValues, laneDetails]);

  useEffect(() => {
    let laneDetails = form.getFieldValue('lanes');
    laneDetails[laneName].totalPiecesError = totalPiecesError ? '0' : '1';
    laneDetails[laneName].totalWeightError = totalWeightError ? '0' : '1';
    form.setFieldValue('lanes', laneDetails);
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [totalPiecesError, totalWeightError]);

  return (
    <div className='h-100 overflow-hidden py-20'>
      <Row className={'h-100 flex-direction-column flex-nowrap'}>
        <Col className='px-30 border-bottom-border'>
          <div className='mb-10'>
            <Row gutter={30}>
              <Col sm={12}>
                <StopCard
                  showHeader={true}
                  stopType={SHIPMENT_STOP_TYPES.ORIGIN}
                  name={laneName}
                  laneDetails={laneDetails}
                />
              </Col>
              <Col sm={12}>
                <StopCard
                  showHeader={true}
                  stopType={SHIPMENT_STOP_TYPES.DEST}
                  name={laneName}
                  laneDetails={laneDetails}
                />
              </Col>
            </Row>
          </div>
        </Col>
        <Form.List name={[laneName, 'stops']} initialValue={[]}>
          {(stops, { add: addStop, remove: removeStop }) => (
            <>
              <Col className='border-bottom-border'>
                <div className='px-30 py-10'>
                  <Row
                    justify={'space-between'}
                    align={'middle'}
                    className={'flex-nowrap'}
                  >
                    <Col flex={1}>
                      <div>
                        <Row
                          gutter={30}
                          align={'middle'}
                          className={'flex-nowrap'}
                        >
                          <Col>
                            <PrimaryLabel className={'white-space-nowrap'}>
                             {t('translation:rateShop.rateShop.stopsDetails')}
                            </PrimaryLabel>
                          </Col>
                          <Col>
                            <FormCheckBox
                              label={t('translation:default.fields.selectAll')}
                              checkBoxClass={'normal-checkbox'}
                              className={'m-0'}
                              checked={
                                selectedStopsIndex?.length ===
                                  stopList?.length && stopList?.length > 0
                              }
                              onChange={handleSelectAll}
                            />
                          </Col>
                          <Col sm={15} lg={10}>
                            <StopsFilter />
                          </Col>
                        </Row>
                      </div>
                    </Col>
                    <Col>
                      <div>
                        <Row gutter={15}>
                          <Col>
                            <Button
                              onClick={() =>
                                addStop({
                                  editMode: true,
                                  isNew: true,
                                  legMile: 0.0,
                                  tripMile: 0.0,
                                })
                              }
                            >
                            {t('translation:rateShop.rateShop.addStop')}
                            </Button>
                          </Col>
                          <Col>
                            <Popconfirm
                              placement='topRight'
                              title={t('translation:default.popConfirm.title')}
                              onConfirm={() => removeStop(selectedStopsIndex)}
                              okText={t('translation:default.buttons.delete')}
                              cancelText={t('translation:default.options.no')}
                              overlayClassName='delete-popup-container'
                              okType={'danger'}
                            >
                              <Button
                                variant='danger'
                                type='default'
                                disabled={selectedStopsIndex?.length === 0}
                              >
                               {t('translation:rateShop.rateShop.deleteStop')}
                              </Button>
                            </Popconfirm>
                          </Col>
                        </Row>
                      </div>
                    </Col>
                  </Row>
                </div>
              </Col>
              {(totalPiecesError || totalWeightError) &&
                laneDetails?.stops?.length > 0 && (
                  <Col className='border-bottom-border py-10 px-30'>
                    {totalPiecesError && (
                      <>
                        <SecondaryLabel className='text-danger f-16 fw-600'>
                          <StarFilled /> {t('translation:rateShop.rateShop.totalPieces')}(
                          {!isNaN(totalStopsPieces) ? totalStopsPieces : '0.00'}
                          ) {t('translation:rateShop.rateShop.totalPiecesEqual')}(
                          {parseFloat(itemsSumValues?.totalPieces).toFixed(4)})
                        {t('translation:rateShop.rateShop.inDetails')}.
                        </SecondaryLabel>
                        <br />
                      </>
                    )}
                    {totalWeightError && (
                      <SecondaryLabel className='text-danger f-16 fw-600'>
                        <StarFilled />  {t('translation:default.fields.totalWeight')}(
                        {!isNaN(totalStopsWeight) ? totalStopsWeight : '0.00'})
                        {t('translation:rateShop.rateShop.totalPiecesEqual')}(
                        {parseFloat(itemsSumValues?.totalWeight).toFixed(4)}) {t('translation:rateShop.rateShop.inDetails')}.
                      </SecondaryLabel>
                    )}
                  </Col>
                )}

              {stops?.length > 0 ? (
                <>
                  <Col className='flex-1 custom-scroll-bar px-20'>
                    <div className='pt-15'>
                      <>
                        {stops.map(({ name: stopName }) => (
                          <StopListCard
                            key={stopName}
                            laneName={laneName}
                            stopName={stopName}
                            form={form}
                            stopDetails={stopList[stopName]}
                            removeStop={removeStop}
                          />
                        ))}
                      </>
                    </div>
                  </Col>
                </>
              ) : (
                <Col className='flex-1 mt-15 px-30'>
                  <EmptyRecords
                    className='h-100'
                    title= {t('translation:rateShop.addNew')}
                    descriptionOne=''
                    descriptionTwo=''
                  />
                </Col>
              )}
            </>
          )}
        </Form.List>
      </Row>
      <HiddenFormInput
        name={[laneName, 'totalPiecesError']}
        rules={[
          () => ({
            validator(_, value) {
              if (value == 0) {
                return Promise.reject(
                  `${t('translation:rateShop.rateShop.totalPieces')}(${
                    !isNaN(totalStopsPieces) ? totalStopsPieces : '0.00'
                  }) ${t('translation:rateShop.rateShop.totalPiecesEqual')}(${parseFloat(
                    itemsSumValues?.totalPieces
                  ).toFixed(4)})${t('translation:rateShop.rateShop.inDetails')}.`
                );
              }
              return Promise.resolve();
            },
          }),
        ]}
      />
      <HiddenFormInput
        name={[laneName, 'totalWeightError']}
        rules={[
          () => ({
            validator(_, value) {
              if (value == 0) {
                return Promise.reject(
                  `${t('translation:default.fields.totalWeight')}(${
                    !isNaN(totalStopsWeight) ? totalStopsWeight : '0.00'
                  }) ${t('translation:rateShop.rateShop.totalPiecesEqual')}(${parseFloat(
                    itemsSumValues?.totalWeight
                  ).toFixed(4)}) ${t('translation:rateShop.rateShop.inDetails')}.`
                );
              }
              return Promise.resolve();
            },
          }),
        ]}
      />
    </div>
  );
};

export default StopDetails;

const StopListCard = ({
  laneName,
  stopName,
  form,
  stopDetails,
  removeStop,
}) => {
  const handleZipDetails = (data) => {
    if (data) {
      let laneDetails = form.getFieldValue('lanes');
      laneDetails[laneName].stops[stopName].stateCode = data?.state;
      laneDetails[laneName].stops[stopName].city = data?.city;
      laneDetails[laneName].stops[stopName].countryCode = data?.countryCode;
      laneDetails[laneName].stops[stopName].postalCode = data?.zip;
      form.setFieldValue('lanes', laneDetails);
    }
  };

  const setEditMode = (value) => {
    let laneDetails = form.getFieldValue('lanes');
    laneDetails[laneName].stops[stopName].editMode = value;
    form.setFieldValue('lanes', laneDetails);
  };

  const handleSave = async () => {
    try {
      await form.validateFields();
      let laneDetails = form.getFieldValue('lanes');
      laneDetails[laneName].stops[stopName].editMode = false;
      laneDetails[laneName].stops[stopName].isNew = false;
      form.setFieldValue('lanes', laneDetails);
    } catch (error) {
      // console.log(error);
    }
  };

  const handleCancel = () => {
    if (stopDetails?.isNew) {
      removeStop(stopName);
    } else {
      let laneDetails = form.getFieldValue('lanes');
      laneDetails[laneName].stops[stopName].editMode = false;
      form.setFieldValue('lanes', laneDetails);
    }
  };

  return (
    <FilterCard
      className='background-white mb-15'
      shadow={true}
      showBorder={true}
      customHeader={
        <div className='w-100'>
          <Row align={'middle'} justify={'space-between'} gutter={30}>
            <Col>
              <FormCheckBox
                label={`${t('translation:rateShop.stop')} #${stopName + 1}`}
                checkBoxClass={'normal-checkbox'}
                className={'m-0'}
                name={[stopName, `selected`]}
              />
            </Col>
            <Col>
              <SaveCancelButtons
                editMode={stopDetails?.editMode}
                setEditMode={setEditMode}
                onSave={handleSave}
                onCancel={handleCancel}
                handleRemove={() => removeStop(stopName)}
              />
            </Col>
          </Row>
        </div>
      }
    >
      <div className='p-15'>
        <StopCard
          formMode={'stop'}
          name={stopName}
          form={form}
          laneDetails={stopDetails}
          handleZipDetails={handleZipDetails}
        />
      </div>
    </FilterCard>
  );
};
