import { Col, Row } from "antd";
import React, { useMemo, useState } from "react";
import SvgConverter from "../../../components/Common/SvgConverter";
import FormLabel from "../../../components/Labels/FormLabel";
import SecondaryLabel from "../../../components/Labels/SecondaryLabel";
import Paper from "../../../components/Paper/Paper";
import CollapsiblePanel from "../../../components/Tabs/CollapsiblePanel";
import { ReactComponent as SaveIcon } from "../../../../assets/icons/save-2.svg";
import RateShopResultCard from "./RateShopResultCard";
import RateShopResultDetailsDrawer from "./RateShopResultDetailsDrawer";
import { useDispatch, useSelector } from "react-redux";
import { setSelectedRateShopCarrierRate } from "../../../../redux/RateShop/RateShopActions";
import { formateWithMoment } from "../../../../utilz/dates";
import EmptyRecords from "../../../components/Common/EmptyRecords";
import ContentSpinner from "../../../components/Spinners/ContentSpinner";
import { useLocation } from "react-router-dom";
import { useTranslation } from 'react-i18next';
import { routeFileSelector } from '../../../../utilz/i18n';

const RateShopResults = () => {
  const [activeTabs, setActiveTabs] = useState(["1"]);
  const { getRateResults } = useSelector((state) => state.rateShop);
  const { pathname: activeTab } = useLocation();
  const namespace = useMemo(() => routeFileSelector(activeTab), [activeTab]);
  const { t } = useTranslation([namespace, 'translation']);

  const rateList = useMemo(() => {
    if (getRateResults?.success) {
      let data = [];
      const rateObj = getRateResults.data;
      for (const key in rateObj) {
        if (Object.hasOwnProperty.call(rateObj, key)) {
          data[key] = rateObj[key];
        }
      }
      return data;
    } else {
      return [];
    }
  }, [getRateResults]);

  return (
    <div className="d-flex flex-direction-column flex-nowrap overflow-hidden h-100">
      <Paper className={"flex-1 overflow-hidden mb-15"}>
        <Row className="h-100 overflow-hidden d-flex flex-direction-column flex-nowrap">
          <Col className="py-20 px-15">
            <Row gutter={30} align={"middle"}>
              <Col sm={3}>
                <FormLabel>{t('translation:rateShop.rateShopResult.shippingDate')}</FormLabel>
                <br />
                <SecondaryLabel className="f-14 fw-600 text-dark">
                  {formateWithMoment(
                    getRateResults?.criteria?.globalRateShopForm?.shipmentDate,
                    "DD-MMM-yyyy"
                  )}
                </SecondaryLabel>
              </Col>
              <Col sm={3}>
                <FormLabel>{t('translation:default.fields.totalWeight')}</FormLabel>
                <br />
                <SecondaryLabel className="f-14 fw-600 text-dark">
                  0
                </SecondaryLabel>
              </Col>
              <Col sm={3}>
                <FormLabel>{t('translation:rateShop.rateShopResult.measurementType')}</FormLabel>
                <br />
                <SecondaryLabel className="f-14 fw-600 text-dark">
                  {getRateResults?.criteria?.globalRateShopForm
                    ?.measurementUit === "LB" && t('translation:rateShop.rateShopResult.imperial')}
                  {getRateResults?.criteria?.globalRateShopForm
                    ?.measurementUit === "KG" && t('translation:rateShop.rateShopResult.metric')}
                </SecondaryLabel>
              </Col>
            </Row>
          </Col>
          <Col className="flex-1 custom-scroll-bar px-15">
            {getRateResults.progressing ? (
              <ContentSpinner size="large" />
            ) : (
              <>
                {Object.keys(getRateResults?.data).length > 0 ? (
                  <CollapsiblePanel
                    forceRender={true}
                    handleTabClick={(keys) => setActiveTabs(keys)}
                    activeKey={activeTabs}
                    tabs={getRateResults.criteria.lanes.map((row, index) => ({
                      tabKey: String(index + 1),
                      content: (
                        <ResultsTabs
                          data={
                            rateList?.[index + 1] ? rateList?.[index + 1] : []
                          }
                        />
                      ),
                      header: (
                        <GenerateTitle
                          title={`${row.ORIGIN_city} - ${row.DEST_city}`}
                          Icon={SaveIcon}
                          size={25}
                          index={index}
                        />
                      ),
                    }))}
                  />
                ) : (
                  <>
                    {getRateResults?.error ? (
                      <EmptyRecords
                        title={t('translation:rateShop.rateShopResult.emptyRecords')}
                        descriptionOne={""}
                        descriptionTwo={""}
                      />
                    ) : (
                      <EmptyRecords
                        title={t('translation:rateShop.rateShopResult.noRatesFound')}
                        descriptionOne={t('translation:default.emptyRecords.descriptionTwo')}
                        descriptionTwo={""}
                      />
                    )}
                  </>
                )}
              </>
            )}
          </Col>
        </Row>
      </Paper>
      <RateShopResultDetailsDrawer />
    </div>
  );
};

const ResultsTabs = ({ data }) => {
  const dispatch = useDispatch();
  const handleSelect = (rateDetails) => {
    dispatch(setSelectedRateShopCarrierRate(rateDetails));
  };
  return (
    <>
      {data.map((row, key) => (
        <RateShopResultCard
          data={row}
          isLast={data.length === key + 1}
          handleSelect={handleSelect}
        />
      ))}
    </>
  );
};

const GenerateTitle = ({ title, Icon, size = 25 }) => {
  return (
    <Row gutter={10} align={"middle"}>
      <Col className="lh-0">
        <SvgConverter width={size} height={size} Icon={Icon} />
      </Col>
      <Col className="lh-0">
        <label className="lh-0 text-dark cursor-pointer fw-700 f-16">
          {title}
        </label>
      </Col>
    </Row>
  );
};

export default RateShopResults;
