import { useEffect } from "react";
import { useDispatch, useSelector } from "react-redux";
import { getMeasurementUnits } from "../lookups/LookupsActions";

const useMeasurementUnits = () => {
  const { measurementUnits } = useSelector((state) => state.lookups);
  const dispatch = useDispatch();

  useEffect(() => {
    if (!measurementUnits?.isFetched) {
      dispatch(getMeasurementUnits());
    }
  }, []);

  return measurementUnits?.progressing ? false : measurementUnits?.data;
};

export default useMeasurementUnits;
