import { Col, Row } from "antd";
import React from "react";
import LaneForm from "./LaneForm";

const LaneDetails = ({ name, data, form }) => {
  const handleDataChanges = (laneData, name) => {
    let laneDetails = form.getFieldValue("lanes");
    laneDetails[name] = laneData;
    form.setFieldValue("lanes", laneDetails);
  };
  return (
    <Row gutter={15} className={"mt-15"}>
      <Col sm={12}>
        <LaneForm
          laneType={"1"}
          name={name}
          data={data}
          handleDataChanges={handleDataChanges}
        />
      </Col>
      <Col sm={12}>
        <LaneForm
          laneType={"2"}
          name={name}
          data={data}
          handleDataChanges={handleDataChanges}
        />
      </Col>
    </Row>
  );
};

export default LaneDetails;
