import React from "react";
import useMeasurementUnits from "../../../redux/hooks/useMeasurementUnits";
import FormSelectBox from "../Inputs/FormSelectBox";
import i18n from 'i18next';

const MeasurementUnitsSelector = ({
  placeholder = i18n.t('translation:default.fields.measurementUit.placeholder'),
  label = i18n.t('translation:default.fields.measurementUit.label'),
  rules = [],
  name,
  editMode,
  value,
  tooltip,
  margin = "mb-15",
  mode = "",
}) => {
  const measurementUnits = useMeasurementUnits();

  return (
    <FormSelectBox
      loading={measurementUnits === undefined}
      options={
        measurementUnits
          ? measurementUnits.map((row) => ({
              label: row.codeDesc,
              value: row.codeId,
            }))
          : []
      }
      placeholder={placeholder}
      label={label}
      margin={margin}
      rules={rules}
      name={name}
      editMode={editMode}
      value={value}
      tooltip={tooltip}
      mode={mode}
    />
  );
};

export default MeasurementUnitsSelector;
