import { Col, Row } from "antd";
import React, { useEffect } from "react";
import { useDispatch, useSelector } from "react-redux";
import {
  getAccessorialsListInProgress,
  getCarriersByCustomerInProgress,
  getTaxListInProgress,
} from "../../../../redux/RateShop/RateShopActions";
import AccessorialsList from "./AccessorialsList";
import CarrierList from "./CarrierList";
import TaxList from "./TaxList";

const AdditionalDetails = () => {
  const { selectedCompany } = useSelector((state) => state.auth);
  const dispatch = useDispatch();

  useEffect(() => {
    if (selectedCompany?.companyId) {
      dispatch(
        getCarriersByCustomerInProgress({
          customerKey: selectedCompany?.companyId,
        })
      );
    }
  }, [selectedCompany]);

  useEffect(() => {
    dispatch(
      getAccessorialsListInProgress({
        PageNumber: 1,
        PageSize: 2000,
        SearchValue: "",
      })
    );
    dispatch(getTaxListInProgress());
  }, []);

  return (
    <Row className="flex-direction-column h-100">
      <Col className="flex-1 overflow-hidden pb-15">
        <CarrierList />
      </Col>
      <Col className="flex-1 overflow-hidden pb-15">
        <AccessorialsList />
      </Col>
      <Col className="flex-1 overflow-hidden pb-15">
        <TaxList />
      </Col>
    </Row>
  );
};

export default AdditionalDetails;
